//https://oleg-agapov.medium.com/basic-single-page-application-using-vue-js-and-firebase-part-2-143a3084266f
import { firebase, auth } from "@/helpers/clientFirebase";
import router from "@/router";

export default {
  state: {
    appVerifier: "",
  },
  getters: {},
  mutations: {
    setAppVerifier(state, payload) {
      state.appVerifier = payload;
    },
  },
  actions: {
    login({ commit }, { email, password }) {
      commit("setLoading", true);
      auth
        .signInWithEmailAndPassword(email, password)
        .then((res) => {
          console.log("signInWithEmailAndPassword", res.user);
          commit("setUser", res.user);
          commit("setLoading", false);
          router.replace({ name: "Menu" });
        })
        .catch((e) => {
          commit("setError", e);
          commit("setLoading", false);
        });
    },

    register({ commit }, { email, password }) {
      commit("setLoading", true);
      auth
        .createUserWithEmailAndPassword(email, password)
        .then((res) => {
          console.log("createUserWithEmailAndPassword", res.user);
          commit("setUser", res.user);
          commit("setLoading", false);
          router.replace({ name: "Menu" });
        })
        .catch((e) => {
          commit("setError", e);
          commit("setLoading", false);
        });
    },

    registerAnomin({ commit }) {
      commit("setLoading", true);
      console.log("registerAnomin statt");
      auth
        .signInAnonymously()
        .then((res) => {
          console.log("signInAnonymously", res.user);
          commit("setUser", res.user);
          commit("setLoading", false);
          router.replace({ name: "Menu" });
        })
        .catch((e) => {
          commit("setError", e);
          commit("setLoading", false);
        });
    },

    logout({ commit }) {
      console.log("logout start");
      auth
        .signOut()
        .then(() => {
          commit("setUser", null);
          commit("clearOrder");
          router.replace({ name: "Auth" });
        })
        .catch((e) => {
          commit("setError", e);
          commit("setLoading", false);
        });
    },

    // ================= AUTH WITH OTP Start =================
    // https://github.com/devatquarxss/phone-password-authentication-firebase
    // REDISTER
    sendOtp({ commit, state }, payload) {
      auth
        .signInWithPhoneNumber(payload.phone, state.appVerifier)
        .then((confirmationResult) => {
          // SMS sent. Prompt user to type the code from the message, then sign the
          // user in with confirmationResult.confirm(code).
          window.confirmationResult = confirmationResult;
          //
          alert("SMS sent");
        })
        .catch((e) => commit("setError", e));
    },
    verifyOtp({ commit }, payload) {
      window.confirmationResult
        .confirm(payload.otp)
        .then((res) => {
          commit("setUser", res.user);
          router.replace({ name: "Menu" });
        })
        .catch((e) => commit("setError", e));
    },

    initReCaptcha({ commit }) {
      console.log("initReCaptcha START");
      setTimeout(() => {
        const recaptchaVerifier = new firebase.auth.RecaptchaVerifier("recaptcha-container", {
          size: "invisible",
          callback: (res) => {
            console.log("RecaptchaVerifier callback", res);
          },
          "expired-callback": (res) => {
            console.log("RecaptchaVerifier expired-callback", res);
          },
        });
        //
        console.log("initReCaptcha", recaptchaVerifier);
        commit("setAppVerifier", recaptchaVerifier);
      }, 1000);
    },

    // LOGIN
    loginPhpne({ commit }, payload) {
      //
      auth
        .signInWithEmailAndPassword(payload)
        .then((res) => {
          commit("setUser", res.user);
          this.$router.push({ path: "/home" });
        })
        .catch((e) => {
          commit("setError", e);
        });
    },

    // CHANGE PASS
    setPassword({ commit }, payload) {
      var user = auth.currentUser;
      //
      // let newEmail = auth.currentUser.phoneNumber + "@domainName.com";
      // let newPassword = this.password;
      //
      user
        .updateEmail(payload.email)
        .then(function() {
          user
            .updatePassword(payload.password)
            .then((res) => {
              console.lof(res);
              this.$router.push("/home"); //route to app home !
            })
            .catch((e) => {
              commit("setError", e);
            });
        })
        .catch((e) => {
          commit("setError", e);
        });
    },

    // ================= AUTH WITH OTP End =================
  },
};
