import Vue from "vue";
import Vuex from "vuex";
import store from "./modules/store";
import auth from "./modules/auth";
import _ from "lodash";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    loading: false,
    user: null,
    error: null,
    place: null,
    table: null,
    menuItems: [],
    menuGroups: [],
    orderItems: {},
    orders: [],
    waiters: [],
  },

  getters: {
    getUserUid(state) {
      return state.user?.uid;
    },
    getMenuItems(state) {
      const { menuItems, menuGroups, orderItems } = state;
      const temp = menuItems.map((item) => {
        const group = menuGroups.find((group) => group.id === item.group && group);

        return {
          ...item,
          // FROM ORDERS
          qty: orderItems[item.id] || 0,
          amount: orderItems[item.id] * item.price || 0,
          // FROM GROUPS
          rank: (group?.rank || 0) * 100 + (item?.rank || 0),
          groupName: group?.name || "set group",
        };
      });
      return temp;
    },
    getMenuItemsGroupped(state, getters) {
      return _.groupBy(_.orderBy(getters.getMenuItems, "rank", "asc"), "groupName");
    },
    getOrderCart(state, getters) {
      return getters.getMenuItems.filter((item) => item.qty > 0);
    },
    getOrderQty(state, getters) {
      return getters.getMenuItems.reduce((a, b) => a + b.qty, 0) || 0;
    },
    getOrderAmount(state, getters) {
      return getters.getMenuItems.reduce((a, b) => a + b.amount, 0) || 0;
    },
  },

  mutations: {
    setLoading(state, payload) {
      state.loading = payload;
    },
    setUser(state, payload) {
      state.user = payload;
    },
    setError(state, payload) {
      state.error = payload;
    },
    setPlace(state, payload) {
      state.place = payload;
    },
    setTable(state, payload) {
      state.table = payload;
    },
    setMenuItems(state, payload) {
      state.menuItems = payload;
    },
    setMenuGroups(state, payload) {
      state.menuGroups = payload;
    },
    setOrderItems(state, payload) {
      state.orderItems = payload;
    },
    setOrders(state, payload) {
      state.orders = payload;
    },
    setWaiters(state, payload) {
      state.waiters = payload;
    },
    changeOrderItems(state, payload) {
      state.orderItems = { ...state.orderItems, [payload.id]: Math.max(state.orderItems[payload.id] + payload.qty || payload.qty, 0) };
    },
    clearOrder(state) {
      state.orderItems = {};
      // let orderItems = {};
      // state.orderItems = state.menuItems.map((item) => {
      //   orderItems[item.id] = 0;
      // });
    },
  },

  modules: {
    store,
    auth,
  },
});
