import Vue from "vue";
import VueRouter from "vue-router";
import { auth } from "../helpers/clientFirebase";

Vue.use(VueRouter);

const routes = [
  {
    path: "/:place",
    name: "Main",
    meta: { requiresAuth: false },
    component: () => import("../views/Main.vue"),
    children: [
      { path: "", redirect: "menu" },
      {
        path: "menu",
        name: "Menu",
        meta: { requiresAuth: true },
        component: () => import("../views/Menu.vue"),
      },
      {
        path: "cart",
        name: "Cart",
        meta: { requiresAuth: true },
        component: () => import("../views/Cart.vue"),
      },
      {
        path: "orders",
        name: "Orders",
        meta: { requiresAuth: true },
        component: () => import("../views/Orders.vue"),
      },
      {
        path: "waiter",
        name: "Waiter",
        meta: { requiresAuth: true },
        component: () => import("../views/Waiter.vue"),
      },
      {
        path: "auth",
        name: "Auth",
        meta: { requiresAuth: false },
        component: () => import("../views/Auth.vue"),
      },
    ],
  },
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes,
});

router.beforeEach((to, from, next) => {
  const requiresAuth = to.matched.some((x) => x.meta.requiresAuth);

  if (requiresAuth && !auth.currentUser) {
    next({ name: "Auth" });
  } else if (to.name == "Auth" && auth.currentUser) {
    next({ name: "Menu" });
  } else {
    next();
  }
});

// https://stackoverflow.com/questions/45091380/vue-router-keep-query-parameter-and-use-same-view-for-children
router.beforeEach((to, from, next) => {
  function hasQueryParams(route) {
    return !!Object.keys(route.query).length;
  }
  const name = to.name;
  const params = from.params;
  const query = from.query;
  if (!hasQueryParams(to) && hasQueryParams(from)) {
    next({ name, params, query });
  } else {
    next();
  }
});

export default router;
